import Joi from '@hapi/joi';

import { BuildType } from '../../../constants';
import { FileList, FileListSchema } from '../../../types';
import {
  BaseiOSJob,
  BaseiOSJobSchema,
  iOSCredentialsSchema,
  iOSCredentials,
} from '../baseJob';

interface GenericiOSJob extends BaseiOSJob {
  type: BuildType.Generic;
  nativeProjectDirectory: string;
  outputName: string;
  artifactPath: string;
  secrets: iOSCredentials & {
    files?: FileList;
  };
}

const GenericiOSJobSchema = BaseiOSJobSchema.concat(
  Joi.object().keys({
    type: Joi.string().valid(BuildType.Generic),
    nativeProjectDirectory: Joi.string().required(),
    outputName: Joi.string().required(),
    artifactPath: Joi.string().required(),
    secrets: Joi.object({
      files: FileListSchema.required(),
    }).concat(iOSCredentialsSchema),
  }),
);

export { GenericiOSJob, GenericiOSJobSchema };
